.game {
  width: 800px;
  height: 800px;
  background-color: skyblue;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.bird {
  width: 40px;
  height: 40px;
  background-color: yellow;
  border-radius: 50%;
  position: absolute;
  left: 100px;
}

.pipe {
  width: 60px;
  position: absolute;
  background-color: green;
}

.pipe.top {
  top: 0;
}

.pipe.bottom {
  bottom: 0;
}

.start-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
  color: white;
  text-shadow: 2px 2px 2px black;
}

.game-over {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
  color: white;
  text-shadow: 2px 2px 2px black;
  text-align: center;
}

.game-over button {
  display: block;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
}

.score {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 24px;
  color: white;
  text-shadow: 2px 2px 2px black;
} 