.game {
  width: 800px;
  height: 800px;
  background-color: skyblue;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.bird {
  position: absolute;
  left: 50px;
  width: 30px;
  height: 30px;
  background: linear-gradient(135deg, #ffd700 0%, #f4c430 50%, #daa520 100%);
  border-radius: 50% 50% 40% 40%;
  transform: rotate(-10deg);
  box-shadow: 
    -2px 2px 4px rgba(0,0,0,0.2),
    inset -2px -2px 6px rgba(0,0,0,0.2),
    inset 2px 2px 6px rgba(255,255,255,0.3);
  border: 1px solid #daa520;
}

.bird::before {
  content: '';
  position: absolute;
  top: 8px;
  left: -8px;
  width: 15px;
  height: 12px;
  background: linear-gradient(135deg, #ffd700 0%, #f4c430 50%, #daa520 100%);
  border-radius: 50% 50% 0 50%;
  transform: rotate(-20deg);
  animation: flapWings 0.4s infinite;
  box-shadow: 
    -1px 1px 3px rgba(0,0,0,0.2),
    inset -1px -1px 3px rgba(0,0,0,0.2),
    inset 1px 1px 3px rgba(255,255,255,0.3);
  border: 1px solid #daa520;
}

.bird::after {
  content: '';
  position: absolute;
  top: 5px;
  left: 20px;
  width: 12px;
  height: 8px;
  background: linear-gradient(135deg, #ff8c6b 0%, #ff6b6b 50%, #e25555 100%);
  clip-path: polygon(0 50%, 100% 0, 100% 100%);
  box-shadow: -1px 1px 2px rgba(0,0,0,0.2);
}

.bird-eye {
  content: '';
  position: absolute;
  top: 8px;
  left: 8px;
  width: 4px;
  height: 4px;
  background: radial-gradient(circle at 30% 30%, #fff 0%, #000 30%);
  border-radius: 50%;
  box-shadow: 0 0 2px rgba(0,0,0,0.5);
}

.pipe {
  width: 60px;
  position: absolute;
  background: linear-gradient(90deg, #2d5e1e 0%, #4a9c2e 20%, #2d5e1e 100%);
  border: 3px solid #1a3612;
  border-radius: 2px;
  box-shadow: inset -5px 0 10px rgba(0, 0, 0, 0.2);
}

.pipe.top {
  top: 0;
  border-bottom: 8px solid #1a3612;
}

.pipe.top::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: -8px;
  width: 70px;
  height: 15px;
  background: linear-gradient(90deg, #2d5e1e 0%, #4a9c2e 20%, #2d5e1e 100%);
  border: 3px solid #1a3612;
  border-radius: 4px;
}

.pipe.bottom {
  bottom: 0;
  border-top: 8px solid #1a3612;
}

.pipe.bottom::before {
  content: '';
  position: absolute;
  top: -15px;
  left: -8px;
  width: 70px;
  height: 15px;
  background: linear-gradient(90deg, #2d5e1e 0%, #4a9c2e 20%, #2d5e1e 100%);
  border: 3px solid #1a3612;
  border-radius: 4px;
}

.start-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
  color: white;
  text-shadow: 2px 2px 2px black;
}

.game-over {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
  color: white;
  text-shadow: 2px 2px 2px black;
  text-align: center;
}

.game-over button {
  display: block;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
}

.score {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 24px;
  color: white;
  text-shadow: 2px 2px 2px black;
}

@keyframes flapWings {
  0%, 100% { 
    transform: rotate(-20deg) scaleY(1);
    box-shadow: -2px 2px 4px rgba(0,0,0,0.2);
  }
  50% { 
    transform: rotate(-30deg) scaleY(0.8);
    box-shadow: -1px 1px 3px rgba(0,0,0,0.3);
  }
} 